import React, { Component } from "react";

class Albums extends Component {
    constructor(props) {
        super(props);
        this.state = {
            albums: [],
            loading: true
        }
    }
    componentDidMount() {
        fetch("https://jsonplaceholder.typicode.com/photos")
            .then((response) => {
                return response.json();
            })
            .then((myJson) => {
                this.setState({
                    albums: myJson,
                    loading: false
                });
            });
    }
    render() {
        let data;
        if (this.state.loading) {
            data = <div className="container">
                <img className="loader mx-auto d-block" src="https://agupubs.onlinelibrary.wiley.com/ux3/widgets/publication-content/images/spinner.gif" />
            </div>
        } else {
            data = <div className="container news-page">
                {this.state.albums.map((album) => {
                    return (
                        <li style={{ 'listStyle': 'none' }} key={album.id}>
                            <div className="container">
                                <div className="row">
                                    <div className="col-xl-7">
                                        <h6>
                                            {album.title}
                                        </h6>
                                        <img src={album.thumbnailUrl} />
                                    </div>
                                </div>
                            </div>
                        </li>
                    )
                })
                }
            </div >
        }
        return (
            <div>
                {data}
            </div>
        );
    }
}

export default Albums;