import React, { Component } from "react";

class News extends Component {
    constructor(props) {
        super(props);
        this.state = {
            articles: [],
            loading: true
        }
    }
    componentDidMount() {
        fetch("https://newsapi.org/v2/top-headlines?sources=google-news&apiKey=84e33c3911ac4238af7093e16b77329a")
            .then((response) => {
                return response.json();
            })
            .then((myJson) => {
                this.setState({
                    articles: myJson.articles,
                    loading: false
                });
            });
    }
    render() {
        let data;
        if (this.state.loading) {
            data = <div className="container">
                <img className="loader  mx-auto d-block" src="https://agupubs.onlinelibrary.wiley.com/ux3/widgets/publication-content/images/spinner.gif" />
            </div>
        } else {
            data = <div className="container news-page">
                {this.state.articles.map((item, index) => {
                    return (
                        <li style={{ 'listStyle': 'none' }} key={index}>
                            <div className="container">
                                <div className="row">
                                    <h2 style={{ margin: '40px 0px' }} className="col-xl-12">
                                        <a href={item.url} >{item.title}</a>
                                    </h2>
                                    <div className="col-xl-4">
                                        <img style={{ width: '100%' }} src={item.urlToImage} alt="news" />
                                    </div>
                                    <div className="col-xl-7">
                                        <p>
                                            {item.content}
                                        </p>
                                        <h5>Author: {(item.author != null) ? item.author : "Anonymous"}</h5>
                                    </div>
                                </div>
                            </div>
                        </li>
                    )
                })
                }
            </div >
        }
        return (
            <div>
                {data}
            </div>
        );
    }
}

export default News;