import React, { Component } from "react";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import News from "./News";
import Bitcoins from "./Bitcoins";
import USA from "./USA";
import _Posts from "./posts/_Posts";
import Albums from "./Albums";

class Navigation extends Component {
    constructor(props) {
        super(props);
        this.state = {
            visible: false
        };
        this.toggleMenu = this.toggleMenu.bind(this);
    }

    toggleMenu() {
        this.setState({ visible: !this.state.visible });
    }

    render() {
        return (
            <Router>
                <nav className="fixed">
                    <div className="container-fluid d-none d-sm-block">
                        <ul className="nav">
                            <li className="col-lg-1 col-sm-2">
                                <Link to="/">News</Link>
                            </li>
                            <li className="col-lg-1 col-sm-2">
                                <Link to="/bitcoins">Bitcoins</Link>
                            </li>
                            <li className="col-lg-1 col-sm-2">
                                <Link to="/usa">USA</Link>
                            </li>
                            <li className="col-lg-1 col-sm-2">
                                <Link to="/posts">Posts</Link>
                            </li>
                            <li className="col-lg-1 col-sm-2">
                                <Link to="/albums">Albums</Link>
                            </li>
                            <li className="col-lg-1 col-sm-1 offset-sm-1 offset-lg-6">
                                <div id="menuToggle">
                                    <input className="sliding" onClick={this.toggleMenu} type="checkbox" />
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                </div>
                            </li>
                        </ul>
                    </div>
                    <div className="container-fluid d-block d-sm-none">
                        <ul className="nav">
                            <li className="col-lg-1 col-sm-1 offset-sm-1 offset-lg-6">
                                <div id="menuToggle">
                                    <input className="sliding" onClick={this.toggleMenu} type="checkbox" />
                                    <span></span>
                                    <span></span>
                                    <span></span>
                                </div>
                            </li>
                        </ul>
                    </div>
                </nav>
                <div className={this.state.visible === true ? 'container-fluid right' : 'container-fluid rightToggle'} >
                    <div className="sliding__inner">
                        <p>
                            Lorem ipsum dolor sit amet consectetur adipisicing elit. Neque officiis rem nobis eius molestias odio ducimus fugiat laudantium, autem magni tenetur voluptatum corporis sunt, placeat est? Tempore explicabo repudiandae sint.
                        </p>
                    </div>
                </div>
                <Route exact path="/" component={News} />
                <Route path="/bitcoins" component={Bitcoins} />
                <Route path="/usa" component={USA} />
                <Route path="/posts" component={_Posts} />
                <Route path="/albums" component={Albums} />
            </Router >
        );
    }
}

export default Navigation;


