import React from "react";

const Posts = ({ posts, loading }) => {
    if (loading) {
        return <img className="loader mx-auto d-block" src="https://agupubs.onlinelibrary.wiley.com/ux3/widgets/publication-content/images/spinner.gif" />
    }
    return (
        <ul className="list-group mb-4">
            {posts.map(post => (
                <li className="list-group-item" key={post.id} >
                    {post.title}
                </li>
            ))}
        </ul>
    );
}

export default Posts;